.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* New styles for the bookshelf image and form section */
.bookshelf-image {
  width: 30%; /* Adjust the width as needed */
  height: auto; /* Maintain aspect ratio */
  max-height: 400px; /* Prevent the image from being too tall */
  object-fit: cover; /* Adjust this depending on how you want the image to behave */
  position: relative;
  z-index: 1; /* Ensure the image doesn't overlap other content */
}

.container {
  position: relative;
  padding: 20px;
}

.form-section {
  position: relative;
  z-index: 2; /* Ensure form is on top of the image */
  background-color: rgba(255, 255, 255, 0.8); /* Optional: Add background for readability */
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .bookshelf-image {
    max-height: 200px; /* Adjust for smaller screens */
  }
  
  .form-section {
    padding: 10px;
  }
}

